import { localStorageSetData } from '@/util/localStorageData';
import { setCookie } from '@/util/cookie';
import { UserApi } from '@/api/api';
export default {
  data: function data() {
    return {
      ishowimg: true,
      isshowlogin: false,
      isshowreg: false,
      radio: "0",
      discount: "admin",
      password: "123456",
      token: null
    };
  },
  mounted: function mounted() {
    window.history.pushState('forward', null, '#'); //在IE中必须得有这两行

    window.history.forward(1);
  },
  methods: {
    handleShowLogin: function handleShowLogin() {
      var _this = this;

      this.ishowimg = !this.ishowimg;
      setTimeout(function () {
        _this.isshowlogin = !_this.isshowlogin;
      }, 600);
    },
    handleCloselogin: function handleCloselogin() {
      var _this2 = this;

      this.isshowlogin = !this.isshowlogin;
      setTimeout(function () {
        _this2.ishowimg = !_this2.ishowimg;
      }, 600);
    },
    handleLogin: function handleLogin() {
      var _this3 = this;

      var that = this;
      this.$Spin.show();
      UserApi.login({
        discount: this.discount,
        password: this.password
      }).then(function (res) {
        if (res.data.err == 0) {
          localStorageSetData('nowLoginUserCount', that.discount);
          localStorageSetData('nowLoginUserName', res.data.data[0].userName);
          setCookie('token', res.data.token);

          _this3.$Spin.hide();

          _this3.Message('success', res.data.msg);

          _this3.$router.push({
            path: '/Managerindex/index'
          });
        } else {
          _this3.$Spin.hide();

          _this3.Message('error', res.data.msg);
        }
      }).catch(function (err) {
        console.log(err);

        _this3.$Spin.hide();

        _this3.Message('error', err.data.msg);
      });
    },
    // 封装消息提示
    Message: function Message(type, content, duration, closable) {
      var msDuration, msClosable;

      if (duration === null || duration === undefined || duration === '') {
        msDuration = 1.5;
      } else {
        msDuration = duration;
      }

      if (closable === null || closable === undefined || closable === '') {
        msClosable = false;
      } else {
        msClosable = closable;
      }

      this.$Message.destroy();
      this.$Message[type]({
        content: content,
        duration: msDuration,
        closable: msClosable
      });
    }
  }
};